:root {
  --primary-color: #e93d08;
  --secondary-color: #1e90ff;
  --font-color: #000000;
  --title-color: #f3e8e8;
  --icon-color: #f3e8e8;
  --bg-color: linear-gradient(120deg, #5e6ad3 0%, #92bee2 100%);
  --input-bg-color: #ecf3fa;
  --loader-color-bg: rgb(57, 206, 169);
  --loader-color: #ecf3fa;
  --completed-green: rgb(57, 206, 169);
}
[data-theme="dark"] {
  --primary-color: #f30707;
  --secondary-color: #2d1eff;
  --font-color: #ffffff;
  --title-color: #f3e8e8;
  --icon-color: #fbfbfb;

  --bg-color: linear-gradient(120deg, #19191f 0%, #070613 100%);
  --input-bg-color: #000000;
  --loader-color-bg: #0eff2e;
  --loader-color: #8f9490;
  --completed-green: #08b91f;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-image: var(--bg-color);
  color: var(--font-color);
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  color: var(--font-color);
}
header {
  font-size: 2rem;
}

header,
form {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--title-color);
}
form input,
form button {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: var(--input-bg-color);
  color: var(--font-color);
}
input:focus-visible {
  outline: none;
}
form button {
  color: var(--primary-color);
  background: var(--input-bg-color);
  cursor: pointer;
  transition: all 0.3s ease;
}
form button:hover {
  background: var(--primary-color);
  color: var(--input-bg-color);
}
.todo-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-list {
  min-width: 30%;
  list-style: none;
}

.todo {
  margin: 0.5rem;
  background: var(--input-bg-color);
  font-size: 1.5rem;
  color: var(--font-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 1s ease;
}
.filter-todo {
  padding: 1rem;
}
.todo li {
  flex: 1;
}

.trash-btn,
.complete-btn {
  background: var(--primary-color);
  color: var(--icon-color);
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}
.complete-btn {
  background: var(--completed-green);
}
.todo-item {
  padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.fall {
  transform: translateY(10rem) rotateZ(20deg);
  opacity: 0;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

/*CUSTOM SELECTOR */
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background-image: none;
}

/* Custom Select */
.select {
  margin: 1rem;
  position: relative;
  overflow: hidden;
  color: var(--font-color);
}

select {
  color: var(--font-color);
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  width: 12rem;
  background-color: var(--input-bg-color);
}
/* Arrow */
.select::after {
  content: "\25BC";
  position: absolute;
  color: var(--icon-color);

  top: 0;
  right: 0;
  padding: 1rem;
  background: var(--primary-color);
  cursor: pointer;
  pointer-events: none;
}
/* Transition */
/*
  .select:hover::after {
    transform: scale(1.5);
  }
  */
.theme-btn {
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem;
  cursor: pointer;
  background-color: #e7e7e7;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  font-family: "Poppins", sans-serif;
  position: fixed;
  bottom: 0.6rem;
  right: 3.8rem;
  color: var(--font-color);
}
.theme-btn i {
  padding: 5px;
}
.theme-text {
  margin-left: 10px;
}
.fas {
  background-image: var(--icon-color);
}
.theme-icon {
  color: black;
}

/*************       LOADING BAR    **************/
.percent {
  text-align: center;
  font-size: 12px;
}
.loader-wrapper {
  width: 320px;
  margin: 0 auto;
  height: 5px;
  background: var(--loader-color);

  overflow: hidden;
  position: relative;
  border-radius: 5px;
}
#loader {
  height: 100%;
  position: absolute;
  left: 0;
  background: var(--loader-color-bg);

  width: 50%;
  -webkit-transition: width 1s ease-in-out;
  -moz-transition: width 1s ease-in-out;
  -o-transition: width 1s ease-in-out;
  transition: width 1s ease-in-out;
}
#percent {
  font-size: 1.2rem;
}

.theme-wrapper {
  display: fleX;
  flex-direction: row;
  align-items: center;
}
